.nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  background-color: rgb(34, 103, 156);
  padding: 15px;
  font-size: 28px;
  color: white;
}

a {
  color: white;
  text-decoration: none;
}

#student-header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 10px 200px;
}

#student-add {
  position: relative;
  top: 20px;
  width: 200px;
  font-size: 20px;
  padding: 10px;
  border: 2px solid black;
  border-radius: 25px;
  font-weight: 550;

}

.fields {
  position: relative;
  left: 50px;
  width: 78%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top:100px;
  gap: 60px;
  row-gap: 100px;
}



.inp1 {
  height: 40px;
  width: 520px;
  background: transparent;
  border: none;
  font-size: 20px;
}

.inp1:focus {
  background: white;
  border: none;
}

#student-table {
  position: relative;
  left: 0px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 0.4fr;
  background-color: white;
  text-align: left;
  border-radius: 10px;
}

.fix-1 {
  padding: 15px;
text-align: center;
  height: 28px;
  border: 2px solid grey;
  font-size: 20px;
  width:"100vh"
}

#fix-2 {
position: relative;
color: blue;
top: -20px;
}

legend {
  border-color: rgb(59, 58, 58);
}

.fix-3 {
  position: fixed;
  top: 70vh;
  left: 500px;
}

.one {
  position: relative;
  left: -10px;
}

.two {
  position: relative;
  left: 200px;
}

.three {
  position: relative;

  left: -400px;
}

.four {
  position: relative;
  left: -250px;

}

.App {
  height: 100vh;
  background-image: linear-gradient(to bottom,lightblue,pink);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#nav_div{
  width: 100vw;
  height: 50px;
  background-color: blue;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  
}

#s1{
  width:80%;
  margin: 80px auto 20px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
#c1{
  font-size: 40px;
  font-weight: bold;
  font-family: serif;
}
#c2{
  padding-top: 10px;
  text-align: center;
  width: 300px;
  height: 40px;
  background-color: cadetblue;
  font-size: 20px;
  border: 2px solid black;
  border-radius: 40px;
}

#table{
  width:80%;
  margin: 20px auto 0px;
  background-color: aliceblue;
  padding: 10px 10px;
  border-collapse: collapse;
}

.tr td{
  border-bottom: 1px solid grey;
  padding: 20px 10px ;
}

